import { useContext } from 'react'
import BasketItem from './BasketItem'
import { ShopContext } from '../contex'

function BasketList() {

    const {order = [], handleBasketShow} = useContext(ShopContext)
    console.log(order)

    const totalPrice = order.reduce((sum, el) => {
        return sum + el.price * el.quantity
    }, 0)

    return (
        <ul className="collection basket-list">
            <li className="collection-item active blue">–ö–æ—Ä–∑–∏–Ω–∞</li>
            {
                order.length
                    ? order.map((item) => <BasketItem key={item.id} {...item}/>)
                    : <li className="collection-item ">–ö–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞</li>
            }
            <li className="collection-item active blue  btn-order">–û–±—â–∞—è —Å—Ç–æ–π–º–æ—Å—Ç—å: {totalPrice} —Ä—É–±.
                <button className='btn btn-small blue lighten-2' onClick={()=> alert('–ï—Å–ª–∏ –í–∞–º –ø–æ–Ω—Ä–∞–≤–∏–ª–æ—Å—å –¥–∞–Ω–Ω–æ–µ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ, –ø–æ—Å—Ç–∞–≤—å—Ç–µ –ø–æ–∂–∞–ª—É–π—Å—Ç–∞ –∑–≤–µ–∑–¥–æ—á–∫—É –Ω–∞ GitHub :)')}> –û—Ñ–æ—Ä–º–∏—Ç—å</button>
            </li>

            <i
                className='material-icons basket-close '
                onClick={handleBasketShow}>
                close
            </i>
        </ul>
    )
}

export default BasketList